h1{
  text-shadow: 1px 1px 5px black;
}

@media(max-width:768px){
h1{
  font-size: 22px;
  }
}

.container{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 40px;
}

.conteudo{
  text-align: center;
  color: white;
  background:linear-gradient(to right,rgb(0, 238, 255), rgb(0, 162, 255));
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0px 0px 10px black;
  margin-bottom: 50px;
}

@media(max-width:768px){
.conteudo{
  padding: 15px;
  margin-bottom: 50px;
  }
}

input{
  font-size: 20px;
  margin: 30px 0px;
  padding: 8px;
  box-shadow: 0px 0px 5px black;
  outline: none;
}

@media(max-width:768px){
input{
  font-size: 14px;
  padding: 5px;
  }
}
